<template>
    <div class="equipmentManagement">
        <div class="search">
            <el-select clearable size="small" placeholder="请选择素材类型" v-model="search.file_type">
                <el-option v-for="item in fileTypeList" :label="item.value" :value="item.id" :key="item.id"></el-option>
            </el-select>
            <el-input placeholder="请选择素材名称" v-model="search.name" clearable size="small"></el-input>
            <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
            <div class="add">
                <el-button size="small" type="primary" icon="el-icon-plus" class="btn_add" @click="add">新增素材</el-button>
            </div>
        </div>
        <div class="home_table">
            <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                <template v-for="(item, index) in tableHead">
                    <el-table-column :prop="item.prop" align="center" v-if="item.prop!='file'" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" v-else :min-width="item.minWidth" :show-overflow-tooltip='true'>
                        <template slot-scope="scope">
                            <el-image v-if="scope.row.file_type==1" style="width: 18px; height: 18px;" :src="baseUrl+scope.row.file.file_small" :preview-src-list="[baseUrl+scope.row.file.file_path]">
                            </el-image>
                            <video v-else-if="scope.row.file_type==2" :src="baseUrl+scope.row.file.file_path" @click="videoClick(baseUrl+scope.row.file.file_path)" height="18px"></video>
                            <el-link :underline="false" target="_blank" v-else-if="scope.row.file_type==3" type="primary" :href="baseUrl+scope.row.file.file_path">{{scope.row.file.file_name}}</el-link>
                        </template>
                    </el-table-column>
                </template>
                <el-table-column prop="address" label="操作" fixed="right" width="150">
                    <template slot-scope="scope">
                        <el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
                        <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <!-- 新增设备弹窗 -->
        <el-dialog :title="title" class="dialog" :visible.sync="dialogFormVisible" v-dialogDrag width="600px" :close-on-click-modal="false" @close='handleClose'>
            <el-form :model="formData" ref="formData" :rules="rule" label-width="110px">
                <el-form-item label="素材名称" prop="title">
                    <el-input v-model="formData.title" placeholder="请输入素材名称"></el-input>
                </el-form-item>
                <el-form-item label="素材类型" prop="fileType">
                    <el-select placeholder="请选择素材类型" v-model="formData.fileType" @change="typeChange">
                        <el-option v-for="item in fileTypeList" :label="item.value" :value="item.id" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="上传素材">
                    <el-upload v-if="formData.fileType==1" :action="action" :headers="headers" list-type="picture-card" :file-list="fileListImg" :on-preview="handlePictureCardPreview" :limit="limit" :on-remove="handleRemove" :on-success="handleAvatarSuccess" :on-error="handleAvatarError" :on-exceed="handleExceed" :before-upload="beforeAvatarUpload">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-upload v-if="formData.fileType==2||formData.fileType==3" :action="actionFile" :headers="headers" :limit="1" :data="source" :on-exceed="handleExceed" :before-upload="beforeFileUpload" :on-success="fileSuccess" :on-error="handleAvatarError" :file-list="files" list-type="picture-card">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="formData.fileType==3" class="el-upload-list__item-thumbnail" src="@/assets/img/audioIcon.png" alt="">
                            <img v-if="formData.fileType==2" class="el-upload-list__item-thumbnail" src="@/assets/img/videoIcon.png" alt="">
                            <span class="el-upload-list__item-actions">
                                <span class="el-upload-list__item-preview" @click="preview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="fileRemove(file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>

                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit" :disabled='disabled'>确 定</el-button>
            </div>
        </el-dialog>
        <el-image-viewer v-if="imgViewerVisible" :on-close="closeImgViewer" :url-list="imgList" />
    </div>
</template>

<script>
import {
    hashApi,
    materialCenterList,
    materialCenterAdd,
    materialCenterEdit,
    materialCenterDel,
    uploadApi,
    uploadFile,
} from '@/api/index';
import { message, messageType } from '@/assets/js/common.js';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            tableHeight: window.innerHeight - 180,
            inputWidth: '300px',
            imgViewerVisible: false,
            dialogFormVisible: false,
            title: '',//新增编辑标题
            imgList: [],
            action: uploadApi,
            actionFile: uploadFile,
            search: {
                name: '',
                address: '',
                file_type: '',
            },
            formData: {},
            equipment: [],
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '100', },
                { title: '素材名称', prop: 'title', minWidth: '200', },
                { title: '素材类型', prop: 'file_type_txt', minWidth: '100', },
                { title: '素材预览', prop: 'file', minWidth: '150', },
                { title: '素材时长', prop: 'file_length_txt', minWidth: '120', },
                { title: '上传时间', prop: 'create_time', minWidth: '150', },
            ],
            tableData: [],
            rule: {
                fileType: [
                    { required: true, message: '请选择素材类型', trigger: 'change' },
                ],
                title: [
                    { required: true, message: '请输入素材名称', trigger: 'blur' },
                ],
            },
            fileTypeList: [],//设备类型列表
            positionData: [],//设备所在位置
            hash: '',
            deviceList: [],//设备列表
            dutyStation: [],//值班地点列表
            poinType: [],//点位类型列表
            baseUrl: '',
            fileListImg: [],//上传图片列表
            fileList: [],//图片id
            dialogImageUrl: '',
            fileId: [],//上传文件id列表
            files: [],//上传文件列表
            source: {
                source: 'pc',
                source_id: '0'
            },
            audioDuration: '',//文件时长
            limit: 9,
            disabled: false,
        };
    },
    computed: {
        headers() {
            return {
                "token": localStorage.getItem("token")// 图片上传获取token
            }
        },
    },
    components: {
        'el-image-viewer': () => import('element-ui/packages/image/src/image-viewer')
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        this.getList();
        this.getHash();
    },
    methods: {
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        videoClick(e) {
            window.open(e, '_blank')
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        //图片删除
        handleRemove(file, fileList) {
            if (file.status != 'success') {
                return
            }
            let arr = this.fileListImg;
            arr.map((item, i) => {
                if (item.file_id == file.file_id) {
                    this.fileList.splice(i, 1)
                    this.fileListImg.splice(i, 1)
                }
            })
        },
        //上传图片之前处理参数数据
        beforeAvatarUpload(file) {
            this.disabled = true;
            const isJPEG = file.type === "image/jpeg";
            const isPNG = file.type === "image/png";
            const isLt1M = file.size / 1024 / 1024 < 10;
            if (!isJPEG && !isPNG) {
                this.$message.error("上传图片只能是 JPG、PNG、JPEG 格式!");
                this.disabled = false;
                return false;
            }
            else if (!isLt1M) {
                this.$message.error("上传图片大小不能超过 10MB!");
                this.disabled = false;
                return false;
            }
        },
        // 上传失败
        handleAvatarError(res, file) {
            message(res.msg);
        },
        //限制上传数量
        handleExceed(files, fileList) {
            this.$message.warning(`当前限制选择 ${fileList.length} 个文件`);
        },
        //图片上传成功
        handleAvatarSuccess(res, file) {
            if (res.code == 1) {
                this.fileList.push(res.data.file_id);
                this.fileListImg.push({ url: this.baseUrl + res.data.file_path, file_id: res.data.file_id });
                this.disabled = false;
            } else {
                message(res.msg)
            }
        },
        // 查看图片-通用
        handlePictureCardPreview(file) {
            this.showImgViewer();
            this.imgList = [file.url]
        },
        showImgViewer() {
            this.imgViewerVisible = true;
            const m = (e) => { e.preventDefault() };
            document.body.style.overflow = 'hidden';
            document.addEventListener("touchmove", m, false); // 禁止页面滑动
        },
        closeImgViewer() {
            this.imgViewerVisible = false;
            const m = (e) => { e.preventDefault() };
            document.body.style.overflow = 'auto';
            document.removeEventListener("touchmove", m, true);
        },
        //视频上传限制
        handleExceed(files, fileList) {
            this.$message.warning(`只可上传 ${files.length} 个文件`);
        },
        beforeFileUpload(file) {
            this.disabled = true;
            let name = file.name.substring(file.name.lastIndexOf(".") + 1)
            let filemp4 = file.size / 1024 / 1024 < 200
            let filemp3 = file.size / 1024 / 1024 < 50
            let videoType = ['MP4', 'mp4', 'mov', 'avi', 'wmv', 'flv', 'asf', 'f4v'];
            if (this.formData.fileType == 2) {
                if (videoType.indexOf(name) == -1) {
                    this.$message.error("上传视频只能是MP4、mp4、mov、avi、wmv、flv、asf、f4v格式!");
                    return false;
                } else if (!filemp4) {
                    this.$message.error("上传视频大小不能超过 200MB!");
                    return false;
                }
            } else if (this.formData.fileType == 3) {
                if (['mp3', 'MP3'].indexOf(name) == -1) {
                    this.$message.error("上传音频只能是mp3格式!");
                    return false;
                } else if (!filemp3) {
                    this.$message.error("上传视频大小不能超过 50MB!");
                    return false;
                }
            }
            this.getTimes(file); //
        },
        //文件上传成功
        fileSuccess(res, file) {
            if (res.code == 1) {
                messageType('上传成功！', 'success')
                this.fileId = [];
                this.files = [];
                this.fileId.push(res.file_id);
                this.files.push({ url: this.baseUrl + res.url, file_id: res.file_id });
                this.disabled = false;
            } else {
                message(res.msg)
            }
        },
        //文件预览
        preview(file) {
            if (!file.response) {
                window.open(file.url ? file.url : this.baseUrl + file.file_path, '_blank')
            } else {
                window.open(this.baseUrl + file.response.url, '_blank')
            }
        },
        //文件删除
        fileRemove(file) {
            this.fileList = [];
            this.files = [];
        },
        add() {
            this.title = '新增素材';
            this.limit = 9;
            this.dialogFormVisible = true;
        },
        edit(row) {
            this.disabled = false;
            this.title = '编辑素材';
            this.limit = 1;
            this.dialogFormVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.formData = row;
                this.formData.fileType = row.file_type;
                this.audioDuration = row.file_length;
                if (row.file_type == 1) {
                    this.fileListImg.push({
                        url: this.baseUrl + row.file.file_small, file_id: row.file.id
                    });
                    this.fileList.push(row.file_id);
                } else if (row.file_type == 2 || row.file_type == 3) {
                    this.fileId.push(row.file_id)
                    this.files.push(row.file);
                }
            })
        },
        typeChange(e) {
            this.files = [];
            this.fileList = [];
            this.fileListImg = [];
            this.fileId = [];
        },
        resetForm() {
            this.formData = {
                title: '',
                fileType: '',
            };
            this.files = [];
            this.fileList = [];
            this.fileListImg = [];
            this.fileId = [];
            this.audioDuration = '';
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        handleClose() {
            this.resetForm()
            this.dialogFormVisible = false;
        },
        async getList() {
            let data = {
                page: this.page,
                pageSize: this.pageSize,
                fileType: this.search.file_type,
                title: this.search.name,
            }
            const res = await this.axios.post(materialCenterList, data);
            if (res.data.code == 1) {
                this.fileTypeList = res.data.data.fileTypeList;
                this.tableData = res.data.data.list.data;
                this.total = res.data.data.list.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },

        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'equipmentManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    if (this.formData.fileType == 1) {
                        if (this.fileListImg.length == 0) {
                            this.$message({
                                showClose: true,
                                message: '请上传素材',
                                type: 'error'
                            });
                            return
                        }
                    } else {
                        if (this.files.length == 0) {
                            this.$message({
                                showClose: true,
                                message: '请上传素材',
                                type: 'error'
                            });
                            return
                        }
                    }
                    console.log(this.audioDuration);
                    let data = {
                        title: this.formData.title,
                        fileType: this.formData.fileType,
                        fileLength: this.formData.fileType != 1 ? this.audioDuration : '0',
                        hash: this.hash,
                    }
                    let api;
                    if (this.title == '新增素材') {
                        api = materialCenterAdd;
                        data.fileIds = this.formData.fileType == 1 ? this.fileList.join(',') : this.fileId.join(',');
                    } else {
                        api = materialCenterEdit;
                        data.id = this.formData.id;
                        data.fileId = this.formData.fileType == 1 ? this.fileList.join(',') : this.fileId.join(',');
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogFormVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        delBtn(id) {
            this.$confirm('是否确定删除该素材,请谨慎操作', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                    };
                    const res = this.axios.post(materialCenterDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    this.$notify.error({
                        title: '失败',
                        message: '取消操作'
                    });
                });
        },
        // 获取上传文件时长  
        getTimes(file) {  //获取时长
            var content = file;
            var url = URL.createObjectURL(content);
            //经测试，发现audio也可获取视频的时长
            var audioElement = new Audio(url);
            console.log(audioElement);
            audioElement.addEventListener("loadedmetadata", (_event) => {
                console.log(audioElement);
                this.audioDuration = parseInt(audioElement.duration);
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
        margin-right: 24px;
    }
    .el-input {
        width: auto;
        margin-right: 24px;
    }
    .el-cascader {
        margin-right: 24px;
    }
    .add {
        flex: 1;
        .btn_add {
            float: right;
        }
        .el-input {
            width: 500px;
        }
    }
}
.dialog {
    .el-select {
        width: 100%;
    }
    /deep/ .el-upload-list--picture-card .el-upload-list__item {
        width: 100px;
        height: 100px;
    }
    /deep/ .el-upload--picture-card {
        width: 100px;
        height: 100px;
        line-height: 110px;
    }
}
.el-image-viewer__wrapper {
    z-index: 9999 !important;
}
</style>


